import { AppDispatch } from "./store";
import { bundleStart, bundleComplete } from "./bundles-slice";
import bundle from "../bundler";
import {
	fetchCells,
	fetchCellsComplete,
	fetchCellsError,
	saveCellsError,
} from "./cells-slice";
import axios from "axios";
import { Cell } from "./cell";
import { RootState } from "./store";

export const createBundle = (cellId: string, input: string) => {
	return async (dispatch: AppDispatch) => {
		dispatch(bundleStart({ cellId }));
		const result = await bundle(input);
		dispatch(bundleComplete({ cellId, bundle: result }));
	};
};

export const fetchCellsAction = () => {
	return async (dispatch: AppDispatch) => {
		dispatch(fetchCells());
		try {
			const { data }: { data: Cell[] } = await axios.get("/cells");
			dispatch(fetchCellsComplete(data));
		} catch (err) {
			dispatch(fetchCellsError(err.message));
		}
	};
};

export const saveCellsAction = () => {
	return async (dispatch: AppDispatch, getState: () => RootState) => {
		const {
			cells: { data, order },
		} = getState();
		const cells = order.map((id) => data[id]);
		try {
			await axios.post("/cells", { cells });
		} catch (err) {
			dispatch(saveCellsError(err.message));
		}
	};
};
