import * as esbuild from "esbuild-wasm";

export const unpkgPathPlugin = () => {
	return {
		// name property is for debug usage, if there are several plugin, name will be printed out by esBuild
		name: "unpkg-path-plugin",
		/* 
      set up function will be called automatically by es build
      override centain parts that bundling process by working with build argument 
      two promary ways of interacting with this build process is by attaching event listeners to listen to 
      the unresolved and on load events  
      ESBuild Bundling process:
        1.Figure out where the index.js is store                          onResolve step
        2.Attempt to load up the index.js file                            onLoad step
        3.Parse the index.js file, find any import/require/exports
        4.If there are any import/require/exports, figure out where the requested file is  onResolve step
    */
		setup(build: esbuild.PluginBuild) {
			// Handle root entry file of 'index.js'
			/* 
        filter: regular expression against the file name that esbuild is trying to load      
      */
			build.onResolve({ filter: /(^index\.js$)/ }, () => {
				return { path: "index.js", namespace: "a" };
			});

			// Handle relative paths in a module args.resolveDir gives the directory of last fetched file
			build.onResolve({ filter: /^\.+\// }, (args: any) => {
				return {
					namespace: "a",
					path: new URL(args.path, "https://unpkg.com" + args.resolveDir + "/")
						.href,
				};
			});

			// Handle main file of a module
			build.onResolve({ filter: /.*/ }, async (args: any) => {
				return {
					namespace: "a",
					path: `https://unpkg.com/${args.path}`,
				};
			});
		},
	};
};
