import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { Cell } from "./cell";

import { CellTypes } from "./cell";
interface CellsState {
	loading: boolean;
	error: string | null;
	order: string[];
	data: {
		[key: string]: Cell;
	};
}
// Define the initial state using that type
const initialState: CellsState = {
	loading: false,
	error: null,
	order: ["1", "2"],
	data: {
		"1": {
			id: "1",
			type: "text",
			content: `# welcome to codenote-x
1. Hover between two cells and click +Code or +Note button to add a new cell.
2. Hover over the top right corner of  each cell and click the button to manipulate the cell`,
		},
		"2": {
			id: "2",
			type: "code",
			content: `import React, {useState} from 'react'
import ReactDOM from 'react-dom'

const App=()=>{
  const [counter,setCounter]=useState(1)
  const onClickHandler=()=>{
    setCounter(counter+1)
  }
  return <div>
      <p onClick={onClickHandler}>Click me to add the number: {counter}</p>
    </div>
}
show(<App/>)`,
		},
	},
};
export type Direction = "up" | "down";
interface MoveCellPayload {
	id: string;
	direction: Direction;
}
// deletecell payload is just id
interface InsertCellAfterPayload {
	id: string | null;
	type: CellTypes;
}
interface UpdateCellPayload {
	id: string;
	content: string;
}

export const cellsSlice = createSlice({
	name: "cell",
	// `createSlice` will infer the state type from the `initialState` argument
	initialState,
	reducers: {
		updateCell: (state, action: PayloadAction<UpdateCellPayload>) => {
			state.data[action.payload.id].content = action.payload.content;
		},
		deleteCell: (state, action: PayloadAction<string>) => {
			delete state.data[action.payload];
			state.order = state.order.filter((id) => id !== action.payload);
		},
		moveCell: (state, action: PayloadAction<MoveCellPayload>) => {
			const index = state.order.findIndex((id) => id === action.payload.id);
			const targetIndex =
				action.payload.direction === "up" ? index - 1 : index + 1;
			if (targetIndex < 0 || targetIndex > state.order.length - 1) {
				return;
			}
			state.order[index] = state.order[targetIndex];
			state.order[targetIndex] = action.payload.id;
		},
		insertCellAfter: (state, action: PayloadAction<InsertCellAfterPayload>) => {
			const cell: Cell = {
				content: "",
				type: action.payload.type,
				id: Math.random().toString(36).substring(7),
			};
			state.data[cell.id] = cell;

			const index = state.order.findIndex((id) => id === action.payload.id);
			if (index === -1) {
				state.order.unshift(cell.id);
			} else {
				state.order.splice(index + 1, 0, cell.id);
			}
		},
		fetchCells: (state) => {
			state.loading = true;
			state.error = null;
		},
		fetchCellsComplete: (state, action: PayloadAction<Cell[]>) => {
			state.order = action.payload.map((cell) => cell.id);
			state.data = action.payload.reduce((acc, cell) => {
				acc[cell.id] = cell;
				return acc;
			}, {} as CellsState["data"]);
		},
		fetchCellsError: (state, action: PayloadAction<string>) => {
			state.loading = false;
			state.error = action.payload;
		},
		saveCellsError: (state, action: PayloadAction<string>) => {
			state.error = action.payload;
		},
	},
});

export const {
	updateCell,
	deleteCell,
	moveCell,
	insertCellAfter,
	fetchCells,
	fetchCellsComplete,
	fetchCellsError,
	saveCellsError,
} = cellsSlice.actions;

export default cellsSlice.reducer;
