import { useEffect } from "react";
import CodeEditor from "./code-editor";
import Preview from "./preview";
import Resizable from "./resizable";
import { useAppDispatch, useAppSelector } from "../hooks/hooks";
import { updateCell } from "../store/cells-slice";
import { createBundle } from "../store/action-creators";
import { Cell } from "../store/cell";
import { useCumulativeCode } from "../hooks/use-cumulative-code";
import "./code-cell.css";

interface CodeCellProps {
	cell: Cell;
}

const CodeCell: React.FC<CodeCellProps> = ({ cell }) => {
	const dispatch = useAppDispatch();
	const bundle = useAppSelector((state) => state.bundles[cell.id]);
	const cumulativeCode = useCumulativeCode(cell.id);

	useEffect(() => {
		if (!bundle) {
			dispatch(createBundle(cell.id, cumulativeCode));
			return;
		}
		const timer = setTimeout(async () => {
			dispatch(createBundle(cell.id, cumulativeCode));
		}, 750);

		return () => {
			clearTimeout(timer);
		};
	}, [cumulativeCode, cell.id, dispatch]);

	return (
		<Resizable direction="vertical">
			<div
				style={{
					height: "calc(100% - 10px)",
					display: "flex",
					flexDirection: "row",
				}}
			>
				<Resizable direction="horizontal">
					<CodeEditor
						initialValue={cell.content}
						onChange={(value) =>
							dispatch(updateCell({ id: cell.id, content: value }))
						}
					/>
				</Resizable>
				<div className="progress-wrapper">
					{!bundle || bundle.loading ? (
						<div className="progress-cover">
							<progress className="progress is-small" max="100">
								Loading
							</progress>
						</div>
					) : (
						<Preview code={bundle.code} err={bundle.err} />
					)}
				</div>
			</div>
		</Resizable>
	);
};

export default CodeCell;
