import { createSlice, PayloadAction } from "@reduxjs/toolkit";

// Define a type for the slice state
interface BundlesState {
	[key: string]:
		| {
				loading: boolean;
				code: string;
				err: string;
		  }
		| undefined;
}

// Define the initial state using that type
const initialState: BundlesState = {};
interface BundleStartPayload {
	cellId: string;
}
interface BundleCompletePayload {
	cellId: string;
	bundle: {
		code: string;
		err: string;
	};
}
export const bundlesSlice = createSlice({
	name: "bundle",
	// `createSlice` will infer the state type from the `initialState` argument
	initialState,
	reducers: {
		bundleStart: (state, action: PayloadAction<BundleStartPayload>) => {
			state[action.payload.cellId] = {
				loading: true,
				code: "",
				err: "",
			};
		},
		bundleComplete: (state, action: PayloadAction<BundleCompletePayload>) => {
			state[action.payload.cellId] = {
				loading: false,
				code: action.payload.bundle.code,
				err: action.payload.bundle.err,
			};
		},
	},
});

export const { bundleStart, bundleComplete } = bundlesSlice.actions;

export default bundlesSlice.reducer;
