import "./cell-list.css";
import { useAppSelector, useAppDispatch } from "../hooks/hooks";
import CellListItem from "./cell-list-item";
import AddCell from "./add-cell";
import { Fragment, useEffect } from "react";
import { fetchCellsAction, saveCellsAction } from "../store/action-creators";

const CellList: React.FC = () => {
	const data = useAppSelector((state) => state.cells.data);
	const order = useAppSelector((state) => state.cells.order);
	const cells = order.map((id) => data[id]);
	const dispatch = useAppDispatch();
	useEffect(() => {
		dispatch(fetchCellsAction());
	}, [dispatch]);
	useEffect(() => {
		dispatch(saveCellsAction());
	}, [cells, dispatch]);
	const renderedCells = cells.map((cell) => (
		<Fragment key={cell.id}>
			<CellListItem cell={cell} />
			<AddCell previousCellId={cell.id} />
		</Fragment>
	));

	return (
		<div className="cell-list">
			<AddCell forceVisible={cells.length === 0} previousCellId={null} />
			{renderedCells}
		</div>
	);
};

export default CellList;
